import {SelectionModel} from '@angular/cdk/collections';
import {Component, OnInit, ViewChild} from '@angular/core';
import {MatPaginator, MatTableDataSource} from '@angular/material';
import {ActivatedRoute, Router} from '@angular/router';
import {InputConfig} from 'src/app/core/model/inputconfig/input-config';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-email',
  templateUrl: './email.component.html',
  styleUrls: ['./email.component.scss']
})
export class EmailComponent implements OnInit {
  isEditdisabled = true;
  isDeleteDisabled = true;
  dataSource = new MatTableDataSource<InputConfig>();
  displayedColumns = ['select', 'rule_name', 'tenant_id'];
  selection = new SelectionModel<InputConfig>(true, []);
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(
      private dataService: DataService, private maskEvent: MaskEventService,
      private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
    this.maskEvent.mask('maskApp', 'Loading Configurations..');
    const params= {tenant_id: coreUtils.removeHifen(window.location.hostname)}
    this.dataService.getEmailConfList(params).subscribe(
        (response) => {
          this.dataSource = new MatTableDataSource<InputConfig>(response.data);
          this.maskEvent.unMask('maskApp');
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  onAddClick() {
    this.router.navigate(['emailForm'], {relativeTo: this.route});
  }

  /**
   * Selects all rows if they are not all selected; otherwise clear selection.
   */
  masterToggle() {
    this.isAllSelected() ? this.selection.clear() :
                           this.dataSource.data.forEach(row => {
                             this.selection.select(row);
                           });
  }

  checkButtonPerm = () => {
    if(this.selection.selected.length > 0) {
      this.isEditdisabled = false;
      this.isDeleteDisabled = false;
    } else {
      this.isEditdisabled = true;
      this.isDeleteDisabled = true;
    }
    return this.selection.selected.length > 0 ? false : true
  }
  // onAddClick(){
  //   this.router.navigate(['emailConfCreate'], { relativeTo: this.route });
  // }
}
