import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';

export interface DialogData {
  heading: string;
  text: any;
}

@Component({
  selector: 'app-failure-message-popup',
  templateUrl: './failure-message-popup.component.html',
  styleUrls: ['./failure-message-popup.component.scss']
})
export class FailureMessagePopupComponent implements OnInit {

  heading: string;
  validationErrosArray: any = [];
  processingErrorsArray: any = [];
  constructor(
      public dialogRef: MatDialogRef<FailureMessagePopupComponent>,
      @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  ngOnInit() {
    this.heading = this.data.heading;
    this.formValidationErrorsArray();
    this.processingErrorsArray = this.data.text.processing_errors;
  }

  formValidationErrorsArray() {
    const keys = Object.keys(this.data.text.validation_errors);
    if(this.checkValidationErrorsLength()) {
      keys.forEach(key => {
        let obj = {};
        obj[key] = this.data.text.validation_errors[key];
        this.validationErrosArray.push(obj)
      })
    }
  }

  checkValidationErrorsLength() {
    return Object.keys(this.data.text.validation_errors).length > 0 ? true : false;
  }

  checkProcessingErrorsLength() {
    return this.processingErrorsArray.length > 0 ? true : false;
  }

  onBtnClick(val: boolean): void {
    this.dialogRef.close(val);
  }

  getKey(obj: any) {
    return Object.keys(obj)[0]
  }

  getMessage(obj: any) {
    return obj[Object.keys(obj)[0]]
  }

}

 

