import {HttpClient, HttpErrorResponse, HttpHeaders, HttpParams} from '@angular/common/http';
import {HttpObserve} from '@angular/common/http/src/client';
import {Injectable} from '@angular/core';
import {Observable, throwError} from 'rxjs';
import {catchError} from 'rxjs/operators';

import {StatusService} from '../statusmessage/status.service';

import {HttpService} from './http.service';

@Injectable({providedIn: 'root'})

/* tslint:disable:no-any */
export class AjaxService extends HttpService {
  private baseHeader: HttpHeaders;

  constructor(private http: HttpClient, private statusService: StatusService) {
    super();
    this.initHeaderObject();
  }

  protected initHeaderObject() {
    super.initHeaderObject();
    this.baseHeader = new HttpHeaders(this.headerObject);
  }

  // angular http client implementation
  ajaxCall(postData: any, callType?: string, url?: string): Observable<any> {
    url = url || this.baseUrl;
    callType = callType || 'post';

    const httpOptions: {
      body?: any;
      headers?: HttpHeaders |
          {
            [header: string]: string|string[];
          };
      params?: HttpParams |
          {
            [param: string]: string|string[];
          };
      observe?: HttpObserve;
      reportProgress?: boolean;
      responseType?: 'blob' | 'json' | 'text';
    } = {headers: this.baseHeader, responseType: 'json'};

    /** Object map of the HTTP params. Mutally exclusive with `fromString`. */
    // const httpParams: HttpParams = new HttpParams({fromObject: postData});

    httpOptions.body = JSON.stringify(postData);

    return this.http.request(callType, url, httpOptions)
        .pipe(catchError(err => {
          this.handleError(err);
          return throwError(err);
        }));
  }
  handleError(error: HttpErrorResponse) {
    if (error.status === 0) {
      this.statusService.post('error', error.message, 5);
    }
  }
}
