import {Component, ComponentFactoryResolver, Input, OnInit, Type, ViewChild} from '@angular/core';
import {FormField} from 'src/app/core/model/dynamicForm/fields/form-field';
import {FormFieldComponent} from 'src/app/core/model/dynamicForm/fields/form-field-component';

import {DynamicFormComplexFieldDirective} from '../dynamic-form-complex-fields/dynamic-form-complex-field.directive';
import {DynamicFormSimpleFieldComponent} from '../dynamic-form-simple-field/dynamic-form-simple-field.component';

export class FormFieldContext {
  formField: FormField;

  constructor(formField: FormField) {
    this.formField = formField;
  }
}

@Component({
  selector: 'app-dynamic-form-field',
  template: '<div appDynamicFormComplexField></div>',
  styleUrls: ['./dynamic-form-field.component.scss']
})
export class DynamicFormFieldComponent implements OnInit {
  @ViewChild(DynamicFormComplexFieldDirective)
  fieldContent: DynamicFormComplexFieldDirective;
  field: FormField;
  component: FormFieldComponent;

  @Input()
  set formField(field: FormField) {
    this.field = field;
    this.renderComponent();
  }

  get formField() {
    return this.field;
  }

  constructor(private componentFactoryResolver: ComponentFactoryResolver) {}

  renderComponent() {
    const viewContainerRef = this.fieldContent.viewContainer;
    viewContainerRef.clear();
    const componentFactory =
        this.componentFactoryResolver.resolveComponentFactory(
            this.resolveViewComponent() as Type<any>);
    this.component =
        viewContainerRef.createComponent(componentFactory).instance as
        FormFieldComponent;
    this.component.formField = this.field;
  }

  /**
   * Returns the dynamic component to be rendered.
   * if a special view i.e callback is there otherwise
   * return a simple form field.
   */
  resolveViewComponent() {
    if (this.field.view) {
      switch (this.field.view) {
        case 'dynamic-dropdown':
        default:
          return DynamicFormSimpleFieldComponent;
      }
    } else {
      return DynamicFormSimpleFieldComponent;
    }
  }

  ngOnInit() {}
}
