import { Component, OnInit, Input } from '@angular/core';
import {BasicComponent} from 'src/app/core/basic-component';
import {MaskEvent} from 'src/app/core/model/mask-event';
import {LightermaskEventService} from 'src/app/core/services/lightermask-event.service';

@Component({
  selector: 'app-lightermask',
  templateUrl: './lightermask.component.html',
  styleUrls: ['./lightermask.component.scss']
})
export class LightermaskComponent extends BasicComponent implements OnInit {
  @Input() id: string;

  show = false;
  msg = '';

  constructor(private maskEventService: LightermaskEventService) {
    super();
  }

  ngOnInit() {
    this.createSubscription(
        'maskEvent',
        this.maskEventService.lightermaskingEvent.subscribe((maskEvent: MaskEvent) => {
          if (this.id === maskEvent.id) {
            this.show = maskEvent.show;
            this.msg = maskEvent.msg;
          }
        }));
  }
}
