import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Login} from 'src/app/core/model/login/login';

import {AjaxService} from '../ajax.service';
import {DataResponse} from '../dataResponse';

export class MonitorRepository<T> {
  constructor(private http: AjaxService) {}
  monitorStats(params: any) {
   const url = this.http.baseUrl + 'get_stats';
    // const url = '';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  cardsStats(params: any) {
    const url = this.http.baseUrl + 'get_stats_cards';
     // const url = '';
     return this.http.ajaxCall(params, 'post', url)
         .pipe(tap((resp: DataResponse<any>) => {
           return resp;
         }));
   }

   individualCardStats(params: any, route: string) {
    const url = this.http.baseUrl + route;
     // const url = '';
     return this.http.ajaxCall(params, 'post', url)
         .pipe(tap((resp: DataResponse<any>) => {
           return resp;
         }));
   }
}
