import {Component, OnInit, ViewChild, OnDestroy} from '@angular/core';
import {MatPaginator, MatTableDataSource} from '@angular/material';
import {ActivatedRoute, Router} from '@angular/router';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import { DatePipe } from '@angular/common';
import { timer, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-moniter-stats',
  templateUrl: './moniter-stats.component.html',
  styleUrls: ['./moniter-stats.component.scss']
})
export class MoniterStatsComponent implements OnInit, OnDestroy {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  chartData: any = {};
  dt :any;
  dateitems = [
    {value: 'Today', viewValue: 'Today'},
    {value: 'Past Week', viewValue: 'Past Week'},
    {value: 'Past 30 Days', viewValue: 'Past 30 Days'},
    {value: 'All Time', viewValue: 'All Time'},
    {value: 'Any Time', viewValue: 'Any Time'}
  ];
  selectedDate = "All Time";
  fromDate: string = "";
  toDate: string = "";
  anyTimeFromDate: any;
  anyTimeToDate: any;
  private killTrigger: Subject<void> = new Subject();
  getStatsRespReceived = false;
  firstCall = true;

  constructor(
      private dataService: DataService, private maskEvent: MaskEventService,
      private router: Router, private route: ActivatedRoute, private datepipe: DatePipe) {}

  ngOnInit() {
    this.setDates(new Date(), new Date());
    this.getCardsStatsData('', '');
  }

  getCardsStatsData(fDate, tDate) {
    this.maskEvent.mask('maskApp', 'Loading..')
    const obj = {
      fromDate: fDate,
      toDate: tDate,
      tenant_id: coreUtils.removeHifen(window.location.hostname)
    }
    this.dataService.getCardStatsDetails(obj).subscribe(
      resp => {
        this.getStatsRespReceived = true;
        this.maskEvent.unMask('maskApp');
        this.chartData = resp.data;
        if(resp.data.length > 0) {
          resp.data.forEach(element => {
            element.manualrefresh = false;
          });
        }
      },
      (error) => {
        this.maskEvent.unMask('maskApp');
      });
  }

  refreshStats() {
    this.firstCall = false;
    this.getStatsRespReceived = false;
    let from = new Date();
    let to = new Date();
    switch (this.selectedDate) {
      case "Today" : 
        this.setDates(from, to);
        this.makeStatsCall(from,to);
        break;
      case "Past Week" :
        from.setDate(from.getDate()-7);
        this.setDates(from, to);
        this.makeStatsCall(from,to);
        break;
      case "Past 30 Days" :
        from.setDate(from.getDate()-30);
        this.setDates(from, to);
        this.makeStatsCall(from,to);
        break;
      case "All Time" :
        this.makeStatsCall("","");
        break;
      case "Any Time" :
        this.makeStatsCall("","");
        break;
      default : 
        break;
    }
  }

  isEmpty(obj) {
    for(let key in obj) {
        if(obj.hasOwnProperty(key))
            return false;
    }
    return true;
  }

  dateChanged(value) {
    let from = new Date();
    let to = new Date();
    switch (value) {
      case "Today" : 
        this.setDates(from, to);
        this.makeStatsCall(from,to);
        break;
      case "Past Week" :
        from.setDate(from.getDate()-7);
        this.setDates(from, to);
        this.makeStatsCall(from,to);
        break;
      case "Past 30 Days" :
        from.setDate(from.getDate()-30);
        this.setDates(from, to);
        this.makeStatsCall(from,to);
        break;
      case "All Time" :
        this.makeStatsCall(this.anyTimeFromDate,this.anyTimeToDate);
        break;
      default : 
        break;
    }
  }

  makeStatsCall(from, to) {
    if(from && to) {
      this.getCardsStatsData(this.datepipe.transform(from, 'yyyy-MM-dd'), 
                        this.datepipe.transform(to, 'yyyy-MM-dd'))
    } else {
      this.getCardsStatsData("", "")
    }
  }

  setDates(from, to) {
    const fdate = new Date(from);
    const tdate = new Date(to);
    const monthNames = ["January", "February", "March", "April", "May", "June",
                        "July", "August", "September", "October", "November", "December"
    ];
    this.fromDate = fdate.getDate() + ' ' + monthNames[fdate.getMonth()];
    this.toDate = tdate.getDate() + ' ' + monthNames[tdate.getMonth()];
  }

  showDateFields() {
    return this.selectedDate !== 'All Time' && this.selectedDate !== 'Any Time'
  }

  anyTimeDateChanged(val: any) {
    this.anyTimeFromDate = val.begin
    this.anyTimeToDate = val.end
    this.makeStatsCall(val.begin, val.end)
  }

  getInput(data, id?: any) {
    data.fromDate = this.fromDate;
    data.toDate = this.toDate;
    let elem: HTMLElement = document.getElementById(id);
    if(elem) {
      data.chartHeight = elem.offsetHeight;
      data.chartWidth = elem.offsetWidth;
    }
    return data;
  }

  getWidth(data) {
    return (data.X && data.X <= 4) ? data.X * 25 : 100      //return as percentange
  }

  getHeight(data) {
    return (data.Y && data.Y <= 4) ? data.Y * 150 : 150*4     //Return as pixel
  }

  allowRefresh(cardDetails) {
    let allow = true;
    if(cardDetails.refresh.toLowerCase() === 'none') {
      allow = false
    }
    return allow;
  }

  manualRefresh(cardDetails) {
    let item = this.chartData.filter(item => (item.id === cardDetails.id))[0];
    item['manualrefresh'] = true;
    setTimeout(() => {item['manualrefresh'] = false}, 1000);
  }

  onAddClick() {}

  ngOnDestroy() {
    this.killTrigger.next();
  }
}
