import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup} from '@angular/forms';
import {Router} from '@angular/router';
import {Login} from 'src/app/core/model/login/login';
import {ParticleJson} from 'src/app/core/model/login/particle-inputs';
import {AuthService} from 'src/app/core/services/global/authentication.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {FieldExceptionFormResolver} from 'src/app/field-exception/field-exception-form/field-exception-form-resolver';
import {FieldExceptionFormComponent} from 'src/app/field-exception/field-exception-form/field-exception-form.component';
import {FieldExceptionListComponent} from 'src/app/field-exception/field-exception-list/field-exception-list.component';
import { invalid } from '@angular/compiler/src/render3/view/util';

@Component({
  selector: 'app-login-home',
  templateUrl: './login-home.component.html',
  styleUrls: ['./login-home.component.scss']
})
export class LoginHomeComponent implements OnInit {
  myStyle: object = {};
  myParams: object = {};
  width = 100;
  height = 100;
  invalidLogin: boolean = false;
  failLoginMessage = "";
  disableButton = false;
  buttonText = 'LOGIN';

  loginForm =
      new FormGroup({username: new FormControl(), password: new FormControl()});

  responseLogin: any = {};

  constructor(
      private dataService: DataService, private authService: AuthService,
      private router: Router) {}

  ngOnInit() {
    this.myStyle = ParticleJson.PARTICLE_PARAMS.styles;

    this.myParams = ParticleJson.PARTICLE_PARAMS.params;

    this.loginForm.valueChanges.subscribe(
        formValues => this.handleValueChanges(formValues));
  }

  get Username() {
    return this.loginForm.get('username');
  }
  get Password() {
    return this.loginForm.get('password');
  }
  onSubmit() {
    this.disableButton = true;
    this.buttonText = "LOGGING IN..."
    const loginObj:
        Login = {userName: this.Username.value, password: this.Password.value};
    this.dataService.login(loginObj).subscribe(response => {
      this.disableButton = false;
      this.buttonText = "LOGIN";
      if(response.flag) {
        this.invalidLogin = false;
        this.authService.setUserDetails(response);
      } else {
        this.invalidLogin = true;
        this.failLoginMessage = response.message
        setTimeout(() => {
          this.invalidLogin = false;
        }, 10000);
      }
    }, (error) => {
      this.disableButton = false;
      this.buttonText = "LOGIN";
    });
  }

  isLoggedin() {
    if (this.authService.getUserStatus()) {
      this.redirect();
    }
    return false;
  }

  handleValueChanges(formValues) {
    // console.log(formValues);
  }

  redirect() {
    this.router.navigate(['/home']);
  }
  forgotPassword() {
    this.router.navigate(['/forgotpassword']);
  }
  closeAlert() {
    this.invalidLogin = false;
  }
}
