import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Login} from 'src/app/core/model/login/login';

import {AuthService} from '../../global/authentication.service';
import {AjaxService} from '../ajax.service';
import {DataResponse} from '../dataResponse';
import { coreUtils } from 'src/app/core/util/coreUtils';

export class LoginRepository<T> {
  constructor(private http: AjaxService, private authService: AuthService) {}

  login(loginParams: Login): Observable<DataResponse<any>> {
    const params: any = {};
    params.username = loginParams.userName;
    params.password = loginParams.password;
    params['tenant_id'] = coreUtils.removeHifen(window.location.hostname);

    const url = this.http.baseUrl + 'login';

    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  logout(logoutParams: Login): Observable<DataResponse<any>> {
    const params: any = {};
    params.username = logoutParams.userName;
    params.session_id = logoutParams.session_id;
    params['tenant_id'] = coreUtils.removeHifen(window.location.hostname);

    const url = this.http.baseUrl + 'logout';

    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  queues(roleNameObj: any): Observable<DataResponse<any>> {
    const url = this.http.baseUrl + 'get_queues';
    roleNameObj.session_id = this.authService.getSessionToken();
    roleNameObj['tenant_id'] = coreUtils.removeHifen(window.location.hostname);
    return this.http.ajaxCall(roleNameObj, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }
}
