import {Injectable} from '@angular/core';
import {Observable, of} from 'rxjs';

import {InputConfig} from '../../model/inputconfig/input-config';
import {Login} from '../../model/login/login';
import {resources} from '../../model/resources/resources';
import {User} from '../../model/user/user';
import {AjaxService} from '../ajax/ajax.service';
import {DataResponse} from '../ajax/dataResponse';
import {DynamicRepository} from '../ajax/resources/dynamic-repository';
import {ExceptionRepository} from '../ajax/resources/exception-repository';
import {InputConfRepository} from '../ajax/resources/Input-conf-repository';
import {LoginRepository} from '../ajax/resources/login-repository';
import {MonitorRepository} from '../ajax/resources/monitor-repository';
import {UserRepository} from '../ajax/resources/user-repository';
import {AuthService} from '../global/authentication.service';
import { DashboardRepository } from '../ajax/resources/dashboard-repository';
import { ScannerRepository } from '../ajax/resources/scanner-repository';

@Injectable({providedIn: 'root'})
/* tslint:disable:no-any */
export class DataService {
  private repos: {[key: string]: any} = {};  // Repository for resources

  private initRepos(ajax: AjaxService, auth: AuthService): void {
    this.repos[resources.USER] = new UserRepository<User>(ajax);
    this.repos[resources.LOGIN] = new LoginRepository<Login>(ajax, auth);
    this.repos[resources.INPUT_CONF] =
        new InputConfRepository<InputConfig>(ajax);
    this.repos[resources.EXCEPTION] = new ExceptionRepository<any>(ajax, auth);
    this.repos[resources.DYNAMIC] = new DynamicRepository<any>(ajax);
    this.repos[resources.MONITOR] = new MonitorRepository<any>(ajax);
    this.repos[resources.DASHBOARD] = new DashboardRepository<any>(ajax);
    this.repos[resources.SCANNER] = new ScannerRepository<any>(ajax, auth);
  }

  private getUserRepository(): UserRepository<any> {
    return this.repos[resources.USER] as UserRepository<any>;
  }

  private getLoginRepository(): LoginRepository<any> {
    return this.repos[resources.LOGIN] as LoginRepository<any>;
  }

  private getInputConfRepository(): InputConfRepository<any> {
    return this.repos[resources.INPUT_CONF] as InputConfRepository<any>;
  }

  private getExceptionRepository(): ExceptionRepository<any> {
    return this.repos[resources.EXCEPTION] as ExceptionRepository<any>;
  }

  private getMonitorRepository(): MonitorRepository<any> {
    return this.repos[resources.MONITOR] as MonitorRepository<any>;
  }

  private getDynamicRespository(): DynamicRepository<any> {
    return this.repos[resources.DYNAMIC] as DynamicRepository<any>;
  }

  private getDashboardRespository(): DashboardRepository<any> {
    return this.repos[resources.DASHBOARD] as DashboardRepository<any>;
  }

  private getScannerRepository(): ScannerRepository<any> {
    return this.repos[resources.SCANNER] as ScannerRepository<any>;
  }
 
  createUser(obj?: any): Observable<DataResponse<any>> {
    return this.getUserRepository().createUser(obj);
  }

  login(obj?: any): Observable<DataResponse<any>> {
    // const loginRepo=  new LoginRepository<Login>(this.ajaxserv);
    return this.getLoginRepository().login(obj);
  }

  logout(obj?: any): Observable<DataResponse<any>> {
    return this.getLoginRepository().logout(obj);
  }

  getAllQueues(obj: any): Observable<DataResponse<any>> {
    return this.getLoginRepository().queues(obj);
  }

  getEmailConfList(obj: any): Observable<DataResponse<any>> {
    return this.getInputConfRepository().emailConfList(obj);
  }

  deleteEmailConf(): Observable<DataResponse<any>> {
    return this.getInputConfRepository().delete();
  }

  saveEmailConfig(params?: any): Observable<DataResponse<any>> {
    return this.getInputConfRepository().saveEmailCategorizationConfig(params);
  }


  getExceptions(params?: any): Observable<DataResponse<any>> {
    return this.getExceptionRepository().templateExceptions(params);
  }

  forceTemplate(params?: any): Observable<DataResponse<any>> {
    return this.getExceptionRepository().forceTemplate(params);
  }

  cluster(params?: any): Observable<DataResponse<any>> {
    return this.getExceptionRepository().cluster(params);
  }

  getMonitorDetails(params?: any): Observable<DataResponse<any>> {
    return this.getMonitorRepository().monitorStats(params);
  }

  getCardStatsDetails(params?: any): Observable<DataResponse<any>> {
    return this.getMonitorRepository().cardsStats(params);
  }

  getEachCardDetails(params: any, route: string): Observable<DataResponse<any>> {
    return this.getMonitorRepository().individualCardStats(params, route);
  }
  
  getFieldsandTabs(params, caseId: any): Observable<DataResponse<any>> {
    return this.getDynamicRespository().getTabsandFields(params,caseId);
  }

  getFieldExceptionForm(params, caseId: any): Observable<DataResponse<any>> {
    return this.getDynamicRespository().getFieldValues(params, caseId);
  }

  dynamicServiceCall(params: any): Observable<DataResponse<any>> {
    return this.getDynamicRespository().serviceHit(params);
  }

  movetoVerify(params: any): Observable<DataResponse<any>> {
    return this.getDynamicRespository().move(params);
  }

  unLockFile(params: any): Observable<DataResponse<any>> {
    return this.getDynamicRespository().unLockFile(params);
  }

  getExcelData(params: any): Observable<DataResponse<any>> {
    return this.getDynamicRespository().excelData(params);
  }

  getDashboardData(params?:any):Observable<DataResponse<any>>{
    return this.getDashboardRespository().dashboardData(params)
  }

  getScannerDocs(params?:any):Observable<DataResponse<any>>{
    return this.getScannerRepository().scanDocs(params)
  }

  constructor(private ajaxserv: AjaxService, private authserv: AuthService) {
    this.initRepos(ajaxserv, authserv);
  }
}
