import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Login} from 'src/app/core/model/login/login';

import {AjaxService} from '../ajax.service';
import {DataResponse} from '../dataResponse';
import { AuthService } from '../../global/authentication.service';
import { coreUtils } from 'src/app/core/util/coreUtils';

export class ExceptionRepository<T> {
  constructor(private http: AjaxService,
              private authService: AuthService) {}
  templateExceptions(params: any) {
    const url = this.http.baseUrl + 'get_queue/' + params.queue_id;
    // const url = 'http://10.0.0.132:5001/get_files';
    const nameParam = {
      user: this.authService.getUserName(),
      start: params.start,
      end: params.end,
      tenant_id: coreUtils.removeHifen(window.location.hostname)
    }
    // const url = 'http://10.0.0.132:5001/get_files';
    return this.http.ajaxCall(nameParam, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  forceTemplate(params: any) {
    params['tenant_id'] = coreUtils.removeHifen(window.location.hostname);
    const url = this.http.baseUrl + 'force_template';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  cluster(params: any) {
    params['tenant_id'] = coreUtils.removeHifen(window.location.hostname);
    const url = this.http.baseUrl + 'cluster';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }
}
