import {FormField} from './form-field';

/* tslint:disable:no-any */
export class NumberFormField extends FormField {
  getType(): string {
    return 'number';
  }

  readValue(val: any): number|string {
    if (val === '' || val === undefined || val === null) {
      return '';
    } else {
      return Number(val);
    }
  }

  setLimits() {
    if (this.fieldDef.min_length) {
      this.min = Number(this.fieldDef.min_length);
    }
    if (this.fieldDef.max_length) {
      this.max = Number(this.fieldDef.max_length);
    }
    this.min = this.min || 0;
    this.max = this.max || (Number.MAX_SAFE_INTEGER);
  }
}
