import {Component, Input, OnInit} from '@angular/core';
import {BasicComponent} from 'src/app/core/basic-component';
import {MaskEvent} from 'src/app/core/model/mask-event';
import {MaskEventService} from 'src/app/core/services/mask-event.service';

@Component({
  selector: 'app-mask',
  templateUrl: './mask.component.html',
  styleUrls: ['./mask.component.scss']
})
export class MaskComponent extends BasicComponent implements OnInit {
  @Input() id: string;

  show = false;
  msg = '';

  constructor(private maskEventService: MaskEventService) {
    super();
  }

  ngOnInit() {
    this.createSubscription(
        'maskEvent',
        this.maskEventService.maskingEvent.subscribe((maskEvent: MaskEvent) => {
          if (this.id === maskEvent.id) {
            this.show = maskEvent.show;
            this.msg = maskEvent.msg;
          }
        }));
  }
}
