import {Time} from '@angular/common';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {ValidatorFn} from '@angular/forms/src/directives/validators';

import {ComboFieldData} from '../combo-field-data';
import {FormFieldConfig} from '../form-field-config';
import { Subject } from 'rxjs';

export abstract class FormField {
  fieldDef: FormFieldConfig;
  display_name: string;      // parser name
  type: string;              // field type
  showPasswordText = false;  // toggle for password field
  view: string;
  default: boolean|number|Date|string|{[key: string]: string}|
      ComboFieldData;  // default value
  value: boolean|number|Date|string|{[key: string]: string}|
      ComboFieldData;  // actual value
  data: any[] = [];
  toolTip: string;
  unique_name: string;
  label: string;  // field label
  min: number;    // number validations
  max: number;
  text = 'displayName';
  val = 'name';
  hidden = false;
  formGrp: FormGroup;
  formCtrl: FormControl;
  errors: string[];
  required = false;
  validators?: ValidatorFn[] = [];
  expanded?: boolean;
  children?: FormField[];
  title?: string;
  loading = false;
  minDate?: Date;
  maxDate?: Date;
  color?: string;
  disabled?: boolean;
  tab: string;
  suspicious: boolean;
  suspiciousFieldModified: boolean;
  currentCroppingField: boolean;
  fieldToBeCropped: Subject<any>  = new Subject<any>();
  fieldOnFocus: Subject<any>  = new Subject<any>();
  readValue(val) {
    return val;
  }

  setLimits() {}


  setDefaults() {
    this.value = this.readValue(this.value);
  }

  getType(): string {
    return '';
  }

  getView(): string {
    return undefined;
  }

  setBasicFields(fieldDef: FormFieldConfig) {
    this.fieldDef = fieldDef;
    this.display_name = fieldDef.display_name;
    this.label = fieldDef.display_name;
    this.toolTip = fieldDef.display_name;
    this.unique_name = fieldDef.unique_name;
    this.disabled = !fieldDef.editable;
    this.tab = fieldDef.tab_id;
  }

  initFormField() {
    this.setLimits();
    this.setDefaults();

    this.type = this.getType();
    this.view = this.getView();

    this.setValidations();
  }

  constructor(
      fieldDef: FormFieldConfig,
      value: boolean|number|Date|string|{[key: string]: string}|
      ComboFieldData) {
    this.checkSuspiciousValue(value);
    this.setBasicFields(fieldDef);
    this.initFormField();
  }

  setValidations() {
    this.validators = [];
    if (this.fieldDef.mandatory) {
      this.validators.push(Validators.required);
      if(this.fieldDef.pattern) {
        this.validators.push(this.customValidator)
      }
      this.required = true;
    }
  }

  customValidator(formControl): any {
  if (formControl.pristine) {
     return null;
  }
  const CUSTOM_REGEXP = /\d{2}[a-zA-Z]{5}\d{4}[a-zA-Z]{1}(?:\d{1}|[a-zA-Z]{1})[a-zA-Z]{1}\w/;
  formControl.markAsTouched();
  if (CUSTOM_REGEXP.test(formControl.value)) {
     return null;
  }
  return {
     invalidPattern: true
  };
}

  checkSuspiciousValue(value: any) {
    const filteredValue = this.removeSuspicious(value);
    this.value = filteredValue;
  }

  removeSuspicious(value: any) {
    this.suspiciousFieldModified = false;
    if (value && !Array.isArray(value) &&
        (value.toLowerCase().includes('suspicious') ||
         value.includes('suspicious'))) {
      this.suspicious = true;
      return value.replace(/suspicious/g, '');
      //return value.replace('suspicious','');
    }
    this.suspicious = false;
    return value;
  }
}
