import {Validators} from '@angular/forms';

import {FormFieldConfig} from '../form-field-config';

import {FormField} from './form-field';

/* tslint:disable:no-any */
export class StringFormField extends FormField {
  setBasicFields(fieldDef: FormFieldConfig) {
    super.setBasicFields(fieldDef);
    if (fieldDef.is_dropdown) {
      fieldDef.dropdown_data.splice(0,0,'')
      this.data = fieldDef.dropdown_data;
      this.value = fieldDef.selectedDropdownValue;
    }
  }

  readValue(val: any): string {
    if (val === undefined || val === null) {
      return '';
    } else {
      return val;
    }
  }

  setLimits() {
    if (!this.fieldDef.is_dropdown) {
      if (this.fieldDef.min_length) {
        this.min = Number(this.fieldDef.min_length);
      }
      if (this.fieldDef.max_length) {
        this.max = Number(this.fieldDef.max_length);
      }
    }
  }

  getType() {
    if (this.fieldDef.is_dropdown) {
      return 'dropdown';
    } else if (this.fieldDef.type.toLowerCase() === 'date picker') {
      return 'date';
    } else {
      return 'text';
    }
  }

  getDefaultValue() {
    return this.readValue(this.value);
  }

  setValidations() {
    super.setValidations();
    if (!this.fieldDef.is_dropdown) {
      if (this.min) {
        this.validators.push(Validators.minLength(this.min));
      }
      if (this.max) {
        this.validators.push(Validators.maxLength(this.max));
      }
    }
    if (this.fieldDef.display_name === 'Name') {
      this.validators.push(Validators.pattern(/^[a-zA-Z0-9_-]*$/));
    }
  }
}
