import { Injectable } from '@angular/core';
import {fromEvent, merge, Observable, of, Subject} from 'rxjs';
import {map} from 'rxjs/operators';

import {MaskEvent} from '../model/mask-event';

@Injectable({providedIn: 'root'})

@Injectable({
  providedIn: 'root'
})
export class LightermaskEventService {
  windowResize: Observable<any>;
  windowHeight: Observable<any> = of(window.innerHeight);
  windowWidth: Observable<any> = of(window.innerWidth);

  lightermaskingEvent: Subject<MaskEvent> = new Subject<MaskEvent>();
  constructor() { 
    this.windowResize = merge(of(window), fromEvent(window, 'resize'));
    this.windowHeight = this.windowResize.pipe(map(() => window.innerHeight));
    this.windowWidth = this.windowResize.pipe(map(() => window.innerWidth));
  }
  mask(id: string, msg?: string) {
    this.lightermaskingEvent.next(new MaskEvent(id, msg || 'Loading', true));
  }

  unMask(id: string) {
    this.lightermaskingEvent.next(new MaskEvent(id, '', false));
  }
}
