import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Login} from 'src/app/core/model/login/login';

import {AjaxService} from '../ajax.service';
import {DataResponse} from '../dataResponse';

export class DynamicRepository<T> {
  constructor(private http: AjaxService) {}
  serviceHit(params: any) {
    const url = this.http.baseUrl + 'execute_button_function';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  getTabsandFields(params: any, caseId: string) {
    const url = this.http.baseUrl + 'get_fields/' + caseId;
    // const url = 'http://10.0.0.132:5001/get_fields'
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  getFieldValues(params: any, caseId: string) {
    const url = this.http.baseUrl + 'get_fields/' + caseId;
    // const url = 'http://10.0.0.132:5001/get_fields'
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  unLockFile(params: any) {
    const url = this.http.baseUrl + 'unlock_case';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  excelData(params: any) {
    const url = this.http.baseUrl + 'search_excel';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  move(params: any) {
    const url = this.http.baseUrl + 'move_to_verify';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }
}
