import {Injectable} from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {Callback} from 'src/app/core/model/dynamicForm/callback';
import {BooleanFormField} from 'src/app/core/model/dynamicForm/fields/boolean-form-field';
import {DateFormField} from 'src/app/core/model/dynamicForm/fields/date-form-field';
import {DropdownFormField} from 'src/app/core/model/dynamicForm/fields/dropdown-form-field';
import {FormField} from 'src/app/core/model/dynamicForm/fields/form-field';
import {NumberFormField} from 'src/app/core/model/dynamicForm/fields/number-form-field';
import {StringFormField} from 'src/app/core/model/dynamicForm/fields/string-form-field';
import {FormFieldConfig} from 'src/app/core/model/dynamicForm/form-field-config';
import {FormConfig} from 'src/app/field-exception/model/form-config';

@Injectable({providedIn: 'root'})
/* tslint:disable:no-any */
export class DynamicFormService {
  masterFields: FormField[];
  fieldsArray: FormFieldConfig[];
  mainFormGroup: FormGroup;
  persistedValues: FormConfig;

  constructor() {
    this.mainFormGroup = new FormGroup({});
  }
  buildFields(context: any) {
    this.mainFormGroup = new FormGroup({});
    this.masterFields = [];
    this.readOptions(context);
    this.builldMasterForm();
    return this.masterFields;
  }

  readOptions(context: any) {
    this.fieldsArray = context.fieldConfig;
    this.persistedValues = context.values;
  }

  builldMasterForm() {
    this.fieldsArray.forEach((field: FormFieldConfig) => {
      let formField: FormField;
      if (field.callbacks !== undefined) {
        formField = this.handleCallBacks(field)[0];
      } else {
        formField = this.handleSimpleField(
            field, this.getFieldValue(field.unique_name, field.is_dropdown));
      }
      if (formField !== undefined) {
        this.masterFields.push(formField);
      }
    });
  }

  handleSimpleField(field: FormFieldConfig, value?) {
    let formField: FormField;
    switch (field.data_type) {
      case 'string':
        if (field.is_dropdown) {
          field.dropdown_data = value;
          field.selectedDropdownValue = this.findSelectedDropdownValue(field.unique_name);
        }
        formField = new StringFormField(field, value);
        break;
      case 'integer':
        formField = new NumberFormField(field, value);
        break;
      case 'boolean':
        formField = new BooleanFormField(field, value);
        break;
      default:
    }
    if (formField) {
      this.assignFormGroup(formField);
    }
    return formField;
  }

  handleCallBacks(field: FormFieldConfig) {
    const formFieldsArray: FormField[] = [];
    field.callbacks.forEach((callback: Callback) => {
      let formField: FormField;
      const callbackName = callback.callbackName;
      switch (callbackName) {
        case 'exampleCallback':
          formField = new DropdownFormField(
              field, this.getFieldValue(field.unique_name, true));
          this.assignFormGroup(formField);
          break;
        default:
      }
      if (formField) {
        formFieldsArray.push(formField);
      }
    });
    return formFieldsArray;
  }

  assignFormGroup(formField: FormField) {
    if (formField.type) {
      formField.formGrp = this.mainFormGroup;
      formField.formCtrl =
          new FormControl({value: formField.value, disabled: formField.disabled}, formField.validators);
      formField.formGrp.addControl(formField.unique_name, formField.formCtrl);
    }
  }

  getFieldValue(name: string, isDropdown) {
    if (this.persistedValues) {
      if (isDropdown) {
        return (this.persistedValues.dropdown_values[name] ? this.persistedValues.dropdown_values[name] : []);
      } else {
        return (this.persistedValues.data[name]);
      }
    }
    return undefined;
  }

  findSelectedDropdownValue(name: string) {
    return this.persistedValues.data[name] ? (this.persistedValues.data[name]) : "";
  }

  getFormControls() {
    return this.mainFormGroup;
  }

  lowerCaseFirstLetter(str: string) {
    return str.charAt(0).toLowerCase() + str.slice(1);
  }
}
