  constructor(
      private dataService: DataService, private lightermaskEvent: LightermaskEventService) {}
  
  inputData: any = {};
  cardData: any;
  private killRefershCards: Subject<void> = new Subject();

  @Input()
  set cardInput(data: any) {
    this.inputData = data;
    if(data.refresh === "auto") {
      timer(0, 30000)
        .pipe(
            // This kills the request if the user closes the component
            takeUntil(this.killRefershCards),
            )
        .subscribe(t => {
          this.getCardData(data.fromDate, data.toDate);
        });
    } else {
      this.getCardData(data.fromDate, data.toDate);
    }
  }

  @Input()
  set manualRefresh(val: any) {
    if(val) {
      this.getCardData(this.inputData.fromDate, this.inputData.toDate);
    }
  }
  
  getCardData(fDate, tDate) {
    this.lightermaskEvent.mask('maskCard', 'Loading Card..');
    const obj = {
      fromDate: fDate,
      toDate: tDate,
      tenant_id: coreUtils.removeHifen(window.location.hostname)
    }
    this.dataService.getEachCardDetails(obj, this.inputData.route).subscribe(
      resp => {
        this.lightermaskEvent.unMask('maskCard');
        this.cardData = resp.data
      },
      (error) => {
        this.lightermaskEvent.unMask('maskCard');
      });
  }

  convertToArray(val) {
    val  = val.toString();
    return val.split('');
  }

  ngOnInit() {
  
  }
  
  isEmpty(obj) {
    for (let key in obj) {
      if (obj.hasOwnProperty(key))
        return false;
    }
    return true;
  }

  ngOnDestroy() {
    this.killRefershCards.next();
  }

