import {Component, OnInit} from '@angular/core';
import {FeedBackType} from 'src/app/core/model/feedback-type';
import {Status} from 'src/app/core/model/status';
import {StatusService} from 'src/app/core/services/statusmessage/status.service';

@Component({
  selector: 'app-feedback-message',
  templateUrl: './feedback-message.component.html',
  styleUrls: ['./feedback-message.component.scss']
})
export class FeedbackMessageComponent implements OnInit {
  static readonly FEEDBACK_TYPES: {[key: string]: FeedBackType} = {
    success: new FeedBackType('fa fa-check-circle', 'feedbackmsg successMsg'),
    error: new FeedBackType('fa fa-exclamation-circle', 'feedbackmsg errorMsg'),
    warning: new FeedBackType(
        'fa fa-exclamation-triangle', 'feedbackmsg alertMsg'),
    info: new FeedBackType('fa fa-info-circle', 'feedbackmsg generalMsg'),
    none: new FeedBackType(
        'fa fa-circle-o-notch', 'feedbackmsg generalMsgloader')
  };

  constructor(private statusService: StatusService) {}

  feedBackMessage = '';
  feedBackType: FeedBackType = FeedbackMessageComponent.FEEDBACK_TYPES.loader;
  messageVisible = false;
  messageMaxLength = 360;


  ngOnInit() {
    this.statusService.status.subscribe((status: Status) => {
      this.setStatusMessage(status);
    });
  }

  closeStatus() {
    this.messageVisible = false;
  }

  setStatusMessage(msgObj: Status) {
    this.feedBackMessage = (msgObj.message.length > this.messageMaxLength) ?
        msgObj.message.substr(0, this.messageMaxLength) + '...' :
        msgObj.message;

    this.feedBackType = FeedbackMessageComponent.FEEDBACK_TYPES[msgObj.type];

    if (msgObj.timeout > 0) {
      setTimeout(() => this.messageVisible = false, (msgObj.timeout * 1000));
    }

    this.messageVisible = true;
  }
}
