  get email() {
    return this.emailConfigForm.get('email');
  }

  getErrorMessage() {
    return this.email.hasError('required') ?
        'You must enter a value' :
        this.email.hasError('email') ? 'Not a valid email' : '';
  }


  ngOnInit() {
    this.dataSource.paginator = this.paginator;
    this.maskEvent.windowHeight.subscribe(
        height => (this.screenHeight = height - 190));
  }

  onSubmit() {
    const emailConfigObj = {
      rule_name: this.emailConfigForm.get('ruleName').value,
      rules: {
        from: this.emailConfigForm.get('from').value,
        to: this.emailConfigForm.get('to').value,
        cc: this.emailConfigForm.get('cc').value,
        subjectWords: this.emailConfigForm.get('subjectWords').value,
        bodyWords: this.emailConfigForm.get('bodyWords').value
      }
    };

    this.dataService.saveEmailConfig(emailConfigObj).subscribe(response => {
    });
  }

