import {Component, Input, OnInit} from '@angular/core';
import { ButtonType } from 'src/app/field-exception/model/button-model';

@Component({
  selector: 'app-dynamic-form-buttons',
  templateUrl: './dynamic-form-buttons.component.html',
  styleUrls: ['./dynamic-form-buttons.component.scss']
})
export class DynamicFormButtonsComponent implements OnInit {
  btn: any;
  disable: boolean;

  @Input()
  set butn(btn: any) {
    this.btn = btn;
  }

  @Input() set butnDisabled(value: any) {
    this.disable = value;
  }

  constructor() {}

  ngOnInit() {}

  onSubmit() {}

  getTooltipData(btn: ButtonType) {
    if(this.disable) {
      return JSON.stringify(['Required Fields', btn.required_field_names])
    }
    return '';
  }
}
