import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';

export interface DialogData {
  heading: string;
  text: string;
}

@Component({
  selector: 'app-callback-popup',
  templateUrl: './callback-popup.component.html',
  styleUrls: ['./callback-popup.component.scss']
})
export class CallbackPopupComponent implements OnInit {
  heading: string;
  messages: string;
  constructor(
      public dialogRef: MatDialogRef<CallbackPopupComponent>,
      @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  ngOnInit() {
    this.heading = this.data.heading;
    this.messages = this.data.text
  }

  onBtnClick(val: boolean): void {
    this.dialogRef.close(val);
  }
}
