import {Component, OnInit} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {ActivatedRoute} from '@angular/router';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-exception-form',
  templateUrl: './exception-form.component.html',
  styleUrls: ['./exception-form.component.scss']
})
export class ExceptionFormComponent implements OnInit {
  id: any;
  private sub: any;
  caseId: any;
  fileName: any;
  imagePath: any;
  retrain: string;
  template_name:any;
  constructor(private route: ActivatedRoute, private sanitizer: DomSanitizer) {}

  ngOnInit() {
    this.caseId = this.route.snapshot.queryParams.caseId;
    this.fileName = this.route.snapshot.queryParams.file_name;
    this.template_name = this.route.snapshot.queryParams.template;
    this.retrain = this.route.snapshot.queryParams.retrain ? 'yes' : 'no';

    this.imagePath = this.sanitizer.bypassSecurityTrustResourceUrl(
        'http://3.208.195.34:5021/training/train.html?case_id=' + this.fileName +
        '&file_name=' + this.fileName + '&host_name=' + coreUtils.removeHifen(window.location.hostname) + this.fileName + '&retrain=' + 
        this.retrain +'&template='+this.template_name);

}
}
