import {Injectable} from '@angular/core';
import {Subject} from 'rxjs';
import {Status} from '../../model/status';

@Injectable({providedIn: 'root'})
export class StatusService {
  status: Subject<Status> = new Subject<Status>();
  message: Subject<string> = new Subject<string>();

  constructor() {}

  post(
      type: 'none'|'success'|'warning'|'info'|'error', message: string,
      timeout?: number) {
    const time = (timeout !== null && timeout !== undefined) ? timeout : 5;

    this.status.next({message, type, timeout: time});
  }
}
