import {APP_BASE_HREF} from '@angular/common';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {CoreComponent} from './core/core/core.component';
import {NavtabComponent} from './core/navtab/navtab.component';
import {AuthGuardService} from './core/services/authGuard/auth-guard.service';
import {LoginGuardService} from './core/services/loginGuard/login-guard.service';
import {EmptyComponent} from './core/util/empty.component';
import {GenerateFormComponent} from './dynamicforms/generate-form/generate-form.component';
import {exceptionConfigRoutes} from './exceptions/exception.routing.module.';
import {fieldExceptionConfigRoutes} from './field-exception/field-exception.routing.module';
import {ForgotPasswordComponent} from './login/forgot-password/forgot-password.component';
import {LoginHomeComponent} from './login/login-home/login-home.component';
import {statsRoutes} from './statistics/moniter-stats-routing.module';
import { ScannerComponent } from './scan/scanner/scanner.component';
import { emailConfigRoutes } from './emailconf/emailconf.routing.module.';

const routes: Routes = [
  {
    path: '',
    component: LoginHomeComponent,
    // canActivate: [LoginGuardService],
  },
  {path: 'forgotpassword', component: ForgotPasswordComponent}, {
    path: 'home',
    component: CoreComponent,
    canActivate: [AuthGuardService],
    children: [{
      path: '',
      component: NavtabComponent,
      children: [
        {
          path: 'monitor',
          component: EmptyComponent,
          children: [...statsRoutes]
        },
        {
          path: 'generateForm', 
          component: GenerateFormComponent
        },
        {
          path: 'emailConfig',
          component: EmptyComponent,
          children: [...emailConfigRoutes]
        },
        {
          path: 'scanner',
          component: ScannerComponent
        }
      ]
    }]
  },
  // {path: '404', component: NotFoundcomponent},
  {path: '**', redirectTo: '/home'}
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [{
    provide: APP_BASE_HREF,
    useValue: '/ace',
  }]
})
export class AppRoutingModule {
}
