import {Component, OnInit} from '@angular/core';
import {FormGroup} from '@angular/forms';
import {FormField} from 'src/app/core/model/dynamicForm/fields/form-field';
import {MaskEventService} from 'src/app/core/services/mask-event.service';

import {DynamicFormService} from '../service/dynamic-form-service';

@Component({
  selector: 'app-generate-form',
  templateUrl: './generate-form.component.html',
  styleUrls: ['./generate-form.component.scss']
})
export class GenerateFormComponent implements OnInit {
  screenHeight: number;
  fields: FormField[] = [];
  form: FormGroup = new FormGroup({});
  previousFormState: any = {};

  constructor(
      private dynamicFormSesrvice: DynamicFormService,
      private maskEvent: MaskEventService) {}

  ngOnInit() {
    this.maskEvent.windowHeight.subscribe(
        height => (this.screenHeight = height - 190));
    this.initForm();
  }

  initForm() {
    const context = {
      config: [
        {
          isDisabled: 'Yes',
          toolTip: 'Template Name',
          isMandatory: 'true',
          displayName: 'Template',
          datatype: 'string',
          queue_id: '1'
        },
        {
          isDisabled: 'No',
          toolTip: 'Comments',
          mandatory: 'No',
          displayName: 'Comments',
          datatype: 'string',
          queue_id: '2'
        },
        {
          isDisabled: 'Yes',
          toolTip: 'Rejection Comments',
          datatype: 'string',
          mandatory: 'Yes',
          displayName: 'Rejection Comments',
          queue_id: '5'
        },
        {
          isDisabled: 'Yes',
          toolTip: 'Password',
          isMandatory: 'true',
          displayName: 'Password',
          datatype: 'string',
          queue_id: '6',
          displayType: 'passwordType'
        }
      ]
    };
    this.fields = this.dynamicFormSesrvice.buildFields(context);
    this.form = this.dynamicFormSesrvice.getFormControls();
    this.previousFormState = this.form.value;

    this.form.valueChanges.subscribe(
        formValues => this.handleValueChanges(formValues));
  }

  handleValueChanges(newFormValues: any) {}

  onSubmit() {}
}
