import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Subscription} from 'rxjs';
import {FieldExceptionFormResolver} from 'src/app/field-exception/field-exception-form/field-exception-form-resolver';
import {FieldExceptionFormComponent} from 'src/app/field-exception/field-exception-form/field-exception-form.component';
import {FieldExceptionListComponent} from 'src/app/field-exception/field-exception-list/field-exception-list.component';

import {MenuItems} from '../model/menu/menu-items';
import {AuthService} from '../services/global/authentication.service';
import {MaskEventService} from '../services/mask-event.service';
import {NavtabService} from '../services/navtab.service';
import {DataService} from '../services/serviceBridge/data.service';
import {EmptyComponent} from '../util/empty.component';
import { StatusService } from '../services/statusmessage/status.service';
import { exceptionConfigRoutes } from 'src/app/exceptions/exception.routing.module.';
import { statsRoutes } from 'src/app/statistics/moniter-stats-routing.module';
import { FailedFileViewComponent } from 'src/app/field-exception/failed-file-view/failed-file-view.component';
import { FailedFileListComponent } from 'src/app/field-exception/failed-file-list/failed-file-list.component';
import { coreUtils } from '../util/coreUtils';
import { ExceptionListComponent } from 'src/app/exceptions/exception-list/exception-list.component';
import { ExceptionFormComponent } from 'src/app/exceptions/exception-form/exception-form.component';
declare function require(name: string): any;

@Component({
  selector: 'app-navtab',
  templateUrl: './navtab.component.html',
  styleUrls: ['./navtab.component.scss']
})
export class NavtabComponent implements OnInit {
  menuItems: MenuItems[] = [];
  selectedPath = '';
  userName = '';
  showStats = false;
  showScan = false;
  navTabExpanded: boolean;
  constructor(
      private router: Router, private authService: AuthService,
      private route: ActivatedRoute, private dataService: DataService,
      private maskEvent: MaskEventService,
      private navTabService: NavtabService,
      private statusService: StatusService) {}

  ngOnInit() {
    require('style-loader!./'+ coreUtils.removeHifen(window.location.hostname) + '.scss');
    this.userName = this.authService.getUserName();
    const paramObj = {username: this.userName};
    this.maskEvent.mask('maskApp', 'Loading Queues..');

    this.dataService.getAllQueues(paramObj).subscribe(
      response => {
        this.maskEvent.unMask('maskApp');
        if (response.flag) {
          this.authService.setNavtabItems(response.data.queues);
          this.formRoutes(response.data.queues);
        } else if (response.message === "logout") {
          this.statusService.post('info', "User does not exist. Logging out")
          setTimeout(() => {
            const respObj = {
              data: {
                userName: '',
                sessionToken: ''
              },
              isUserLoggedIn: '',
              userQueues: [],
            };
            this.authService.setUserDetails(respObj);
            this.router.navigate(['']);
          }, (5000))
        }
      },
      (error) => {
        this.maskEvent.unMask('maskApp');
      });

    this.navTabService.currentNavtabState.subscribe(
        message => this.handleTabChanges(message));
  }

  handleTabChanges(value) {
    this.navTabExpanded = value;
  }

  formRoutes(items) {
    items.forEach((item: MenuItems) => {
      if(item.type === "field") {
        this.menuItems.push(item);
        this.findFirstRoute().children[0].children.unshift({
          path: item.path,
          component: EmptyComponent,
          children: [
            {
              path: '',
              component: FieldExceptionListComponent,
            },
            {
              path: '',
              component: EmptyComponent,
              children: [{
                path: item.path + 'form',
                component: FieldExceptionFormComponent,
                resolve: {formContext: FieldExceptionFormResolver}
              }]
            }
          ]
        });
      }
      if(item.type === "failed") {
        this.menuItems.push(item);
        this.findFirstRoute().children[0].children.unshift({
          path: item.path,
          component: EmptyComponent,
          children: [
            {
              path: '',
              component: FailedFileListComponent,
            }]
        });
      }
      if (item.type === "train") {
        this.menuItems.push(item);
        this.findFirstRoute().children[0].children.unshift({
          path: item.path,
          component: EmptyComponent,
          children: [
            {
              path: '',
              component: ExceptionListComponent,
            },
            {
              path: '',
              component: EmptyComponent,
              children: [{
                path: item.path + 'form',
                component: ExceptionFormComponent
              }]
            }
          ]
        });
      } 
      if (item.type === "stats") {
        this.showStats = true;
      }
      if (item.type === "scan") {
        this.showScan = true;
      }
    });

    if(items.length > 0) {
      if(this.showStats) {
        this.router.navigate(
          ['monitor'], {relativeTo: this.route});
        this.selectedPath = 'monitor';
      } else {
        this.routeToPage(items[0])
      }
    }
  }

  routeToPage(item: MenuItems) {
    this.authService.setSelectedQueueId(item.pathId);
    this.selectedPath = item.path;
    this.router.navigate(
        [item.path],
        {queryParams: {pathId: '' + item.pathId}, relativeTo: this.route});
  }

  routeToTraining(item: MenuItems) {
    this.authService.setSelectedQueueId(item.pathId);
    this.selectedPath = 'templateExceptions';
    this.router.navigate(
      ['templateExceptions'], {queryParams: {pathId: '' + item.pathId}, relativeTo: this.route});
  }

  getName(name) {
    return this.navTabExpanded ? '' : name;
  }

  findFirstRoute() {
    return this.router.config.find(item => item.path === 'home');
  }

  routeToScan() {
    const item = this.authService.getQueueByPathname('scanner');
    this.routeToPage(item[0])
  }

}