import {Component, OnInit} from '@angular/core';
import { ConnectionService } from 'ng-connection-service';
import { StatusService } from './core/services/statusmessage/status.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  ngOnInit() {}

  constructor(private connectionService: ConnectionService, private statusService: StatusService) {
    this.connectionService.monitor().subscribe(isConnected => {
      if (!isConnected) {
        this.statusService.post('warning', 'Please check your internet connection or try again later', 60)
      }
    })
  }
}
