import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';
import {Login} from 'src/app/core/model/login/login';

import {AjaxService} from '../ajax.service';
import {DataResponse} from '../dataResponse';
import { coreUtils } from 'src/app/core/util/coreUtils';

export class InputConfRepository<T> {
  constructor(private http: AjaxService) {}

  emailConfList(params: any): Observable<DataResponse<any>> {
    params['tenant_id'] = coreUtils.removeHifen(window.location.hostname);
    const url = this.http.baseUrl + 'stored_config';

    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));

    /**
     * to get particukar email config details post and pass email as input
     * param {'email_id': ...}
     */
  }

  delete(): Observable<DataResponse<any>> {
    const params: any = {};
    const url = '';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  saveEmailCategorizationConfig(params: any) {
    params['tenant_id'] = coreUtils.removeHifen(window.location.hostname);
    const url = this.http.baseUrl + 'store_config';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }

  validateEmail(params: any) {
    const url = 'http://b9e87545.ngrok.io/validate_email';  // validateemail
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          return resp;
        }));
  }
}
