import {Injectable} from '@angular/core';
import {FormFieldConfig} from 'src/app/core/model/dynamicForm/form-field-config';
import {ButtonType} from '../model/button-model';

@Injectable({providedIn: 'root'})
export class FieldStoreService {
  fieldConfig: FormFieldConfig[];
  buttons: ButtonType[];
  tabs: any[];
  tabMap: any;
  excelTables: any;
  retrain: boolean = false;
  constructor() {}

  setFieldConfig(fieldArray: FormFieldConfig[]) {
    this.fieldConfig = fieldArray;
  }

  setQueueButtons(buttons) {
    this.buttons = buttons;
  }

  setQueueTabs(tabs) {
    this.tabs = tabs;
  }

  setTabTypeMapping(map) {
    this.tabMap = map;
  }

  setExcelTypeData(tables) {
    this.excelTables = tables;
  }

  setTraningBtnStatus(val) {
    this.retrain = val;
  }

  getFieldConfig() {
    return this.fieldConfig;
  }

  getQueueButtons() {
    return this.buttons;
  }

  getQueueTabs() {
    return this.tabs;
  }

  getTabTypeMapping() {
    return this.tabMap;
  }

  getExcelTypeData() {
    return this.excelTables;
  }

  getTraningBtnStatus() {
    return this.retrain;
  }

  setTabsandFields(data: any) {
    this.setFieldConfig(data.field);
    this.setQueueButtons(data.buttons);
    this.setQueueTabs(data.tabs);
    this.setTabTypeMapping(data.tab_type_mapping);
    this.setExcelTypeData(data.excel_source_data);
    this.setTraningBtnStatus(data.retrain);
  }
}
