import {Component, OnInit, ViewChild} from '@angular/core';
import {Router} from '@angular/router';

import {NavtabComponent} from '../navtab/navtab.component';
import {AuthService} from '../services/global/authentication.service';
import {NavtabService} from '../services/navtab.service';
import {DataService} from '../services/serviceBridge/data.service';
import { coreUtils } from '../util/coreUtils';
declare function require(name: string): any;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @ViewChild('aceHelp') aceHelp;
  @ViewChild('aceUser') aceUser;
  isHelpSelected: boolean;
  isUserSelected: boolean;
  userName = '';
  roleName = '';
  expanded = false;

  constructor(
      private dataService: DataService, private authService: AuthService,
      private router: Router, private navTabService: NavtabService) {}

  ngOnInit() {
    require('style-loader!./'+ this.getHostName() + '.scss');
    this.userName = this.authService.getUserName();
    this.roleName = this.authService.getUserRole();
  }

  getHostName() {
    const hostname = coreUtils.removeHifen(window.location.hostname);
    const regex = /[0-9]{1-3}\.[0-9]{1-3}\.[0-9]{1-3}\.[0-9]{1-3}/;
    return regex.test(hostname) ? 'localhost' : hostname
  }

  showDropdown(type: string) {
    switch (type) {
      case 'aceHelp':
        this.showHelpDropdown();
        break;
      case 'aceUser':
        this.showUserDropdown();
        break;
    }
  }

  showHelpDropdown() {
    if (!this.isHelpSelected) {
      this.isHelpSelected = true;
      this.updateEventListener('add');
    } else {
      this.closeHelpDropDown();
    }
  }

  showUserDropdown() {
    if (!this.isUserSelected) {
      this.isUserSelected = true;
      this.updateEventListener('add');
    } else {
      this.closeUserDropDown();
    }
  }

  updateEventListener(condition: string) {
    if (condition === 'add') {
      document.addEventListener('click', this.handleClick, false);
    } else {
      document.removeEventListener('click', this.handleClick, false);
    }
  }

  handleClick =
      (event) => {
        if (this.isHelpSelected &&
            !this.aceHelp.nativeElement.contains(event.target)) {
          this.closeHelpDropDown();
        } else if (
            this.isUserSelected &&
            !this.aceUser.nativeElement.contains(event.target)) {
          this.closeUserDropDown();
        }
      }

  closeHelpDropDown() {
    if (!this.isUserSelected) {
      this.updateEventListener('remove');
    }
    this.isHelpSelected = false;
  }

  closeUserDropDown() {
    if (!this.isHelpSelected) {
      this.updateEventListener('remove');
    }
    this.isUserSelected = false;
  }

  logout() {
    const logoutObj = {
      userName: this.authService.getUserName(),
      session_id: this.authService.getSessionToken()
    }
    this.dataService.logout(logoutObj).subscribe(response => {
      if(response.flag) {
        const respObj = {
          data: {userName: '', sessionToken: ''},
          isUserLoggedIn: '',
          userQueues: [],
        };
        this.authService.setUserDetails(respObj);
        this.router.navigate(['']);
      }
    });
    // const respObj = {
    //   data: {userName: '', sessionToken: ''},
    //   isUserLoggedIn: '',
    //   userQueues: [],
    // };
    // this.authService.setUserDetails(respObj);
    // this.router.navigate(['']);
  }

  toggleSidenav() {
    this.expanded = !this.expanded;
    this.navTabService.changeNavtabState(this.expanded);
  }

}
