import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Observable, interval } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-count-up-timer',
  templateUrl: './count-up-timer.component.html',
  styleUrls: ['./count-up-timer.component.scss']
})
export class CountUpTimerComponent implements OnInit {

  receivedTime: number;
  @Input()
  set time(value: any) {
    this.receivedTime = value;
    this.timeSpent = this.findHoursMinsSecs();
  }

   
 // @Output() notifyTime: EventEmitter<any> = new EventEmitter<any>();
  timeSpent: string;

  constructor() { }

  ngOnInit() {
    // interval(1000).pipe(map((time) => {
    //   this.receivedTime = this.receivedTime + 1;
    // })).subscribe((time) => {
    //   this.timeSpent = this.findHoursMinsSecs();
    // //  this.notifyTime.emit(this.findHoursMinsSecs())
    // })
  }

  findHoursMinsSecs() {
    const hours = Math.floor(this.receivedTime/3600);
    let remainingSeconds = this.receivedTime - hours * 3600;
    const minutes = Math.floor(remainingSeconds/60);
    remainingSeconds = remainingSeconds - minutes * 60;
    const seconds = Math.floor(remainingSeconds);

    return [
      hours.toString().length === 1 ? '0' + hours + ' :' : hours + ' :',
      minutes.toString().length === 1 ? '0' + minutes +' :' : minutes + ' :',
      seconds.toString().length === 1 ? '0' + seconds : seconds
    ].join(' ')
  }

}
