import {ValidationErrors} from '@angular/forms';

import {FormField} from './form-field';

const validationErrors = {
  nameExists: 'This name already exists',
  invalidJson: 'Invalid JSON Format',
  required: 'This field is required',
  nonUniqueName: 'Name already exists. Type a different name.',
  pattern:
      'Name must contain alphanumeric characters and _ only . Do not use any special characters.'
};
export class FormFieldComponent {
  formField: FormField;

  getErrors(): string[] {
    const errors: string[] = [];

    const valErros: ValidationErrors = this.formField.formCtrl.errors;
    for (const key in valErros) {
      if (valErros[key]) {
        errors.push(validationErrors[key] || key);
      }
    }

    return errors;
  }
}
