import {Component, OnInit, ElementRef} from '@angular/core';
import {FormFieldComponent} from 'src/app/core/model/dynamicForm/fields/form-field-component';
import { FormField } from 'src/app/core/model/dynamicForm/fields/form-field';

@Component({
  selector: 'app-dynamic-form-simple-field',
  templateUrl: './dynamic-form-simple-field.component.html',
  styleUrls: ['./dynamic-form-simple-field.component.scss']
})
export class DynamicFormSimpleFieldComponent extends FormFieldComponent
    implements OnInit {
  maxDate = new Date();
  ngOnInit() {}

  isMandatoryAndEmpty(formField: FormField) {
    return (formField.required && !this.formField.formGrp.controls[formField.unique_name].value)
  }

  cropFieldClicked(formField: FormField){
    formField.currentCroppingField = true
    formField.fieldToBeCropped.next(formField.unique_name)
  }

  fieldOnFocus(formField: FormField) {
    formField.fieldOnFocus.next(formField)
  }

  testValidity(formGrp, unique_name) {
    return formGrp.get(unique_name).touched && formGrp.get(unique_name).hasError('invalidPattern')
  }
}
