import { Component, OnInit, Inject } from '@angular/core';
import { PdfHandlerService } from 'src/app/field-exception/service/pdfhandler/pdf-handler.service';
import { DomSanitizer } from '@angular/platform-browser';
import { MaskEventService } from 'src/app/core/services/mask-event.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

export interface FailedViewDialogData {
  case_id: string;
}

@Component({
  selector: 'app-failed-file-view',
  templateUrl: './failed-file-view.component.html',
  styleUrls: ['./failed-file-view.component.scss']
})
export class FailedFileViewComponent implements OnInit {

  convImgFromPDF = [];
  file_name = "";
  pdfSrc = {};
  pdfUrl:any;
  imageWidth = 100;
  pdfError = 0;
  disablebutton = false;

  constructor(private sanitizer: DomSanitizer, private maskEvent: MaskEventService,
    public dialogRef: MatDialogRef<FailedFileViewComponent>,
      @Inject(MAT_DIALOG_DATA) public data: FailedViewDialogData) { }

  ngOnInit() {
    this.file_name = this.data.case_id

    this.pdfSrc = {
      "type": "PDF",
      "name": this.file_name + '.pdf',
      "pages": []
    };

    this.pdfUrl = this.sanitizer.bypassSecurityTrustResourceUrl(
      'assets/pdf/' + this.file_name + '.pdf');
  }

  onBtnClick(val: any, filename?: any): void {
    this.dialogRef.close({service: val, file_name: this.file_name});
  }
}
