import {Component} from '@angular/core';  // 1a

/**
 * 2a. Pass in the name for the HTML class selector (<my-app></my-app>)
 * 2b. Set the styles of our component using the styles options
 * 2c. Define our component's template inline, rather than using the templateUrl
 * option to access an external file.
 */
@Component({
  selector: 'app-empty',
  styles: [` .empty {
        display: none;
    }`],
  template: `<div class="empty"></div><router-outlet></router-outlet>`
})
export class EmptyComponent {
}
