import {cloneDeep} from 'lodash';

/* tslint:disable:no-any */
export const coreUtils = {
  overload(target: any, source: any) {
    return Object.assign(target, source);
  },
  clone(source, shallow ? : boolean) {
    if (shallow) {
      return this.overload({}, source);
    } else {
      return cloneDeep(source);
    }
  },
  numerize(obj: any, props: string[]) {
    props.forEach((prop => {
      if (Array.isArray(obj[prop])) {
        obj[prop] = obj[prop].map(arr => Number(arr));
      } else {
        obj[prop] = Number(obj[prop]);
      }
    }));
  },
  stringify(obj: any, props: string[]) {
    props.forEach((prop => {
      if (Array.isArray(obj[prop])) {
        obj[prop] = obj[prop].map(arr => arr + '');
      } else {
        obj[prop] = (obj[prop] + '');
      }
    }));
  },
  lowerCaseFirstLetter(str: string) {
    return str.charAt(0).toLowerCase() + str.slice(1);
  },
  lowerCase(str: string) {
    return str.toLowerCase();
  },
  isString(val): boolean {
    return (
      (val !== null) && (val !== undefined) &&
      (('string' === (typeof val)) || (val instanceof String)));
  },
  createDummyDefaultValue(val) {
    const obj = {};
    Object.keys(val).forEach((key) => obj[key] = '');
    return obj;
  },
  removeHifen(val) {
      return val.split('-')[0]
  }
}