import {Injectable} from '@angular/core';
import {BehaviorSubject, Observable} from 'rxjs';
import {Subject} from 'rxjs/Subject';

@Injectable({providedIn: 'root'})
export class NavtabService {
  constructor() {}

  private tabStateValueSource = new BehaviorSubject(false);

  currentNavtabState = this.tabStateValueSource.asObservable();


  changeNavtabState(message: boolean) {
    this.tabStateValueSource.next(message);
  }
}
