import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {FieldExceptionFormComponent} from 'src/app/field-exception/field-exception-form/field-exception-form.component';
import {FieldExceptionListComponent} from 'src/app/field-exception/field-exception-list/field-exception-list.component';

import {MenuItems} from '../../model/menu/menu-items';

@Injectable({providedIn: 'root'})
export class AuthService {
  selectedQueueId: number;
  navtabItems: MenuItems[];

  constructor(private router: Router) {}

  getUserStatus() {
    return this.isUserSessionAtive() ?
        JSON.parse(localStorage.getItem('userObj')).flag :
        false;
  }
  getUserName() {
    return this.isUserSessionAtive() ?
        JSON.parse(localStorage.getItem('userObj')).data.username :
        '';
  }
  getSessionToken() {
    return this.isUserSessionAtive() ?
        JSON.parse(localStorage.getItem('userObj')).data.session_id :
        '';
  }
  getUserRole() {
    return this.isUserSessionAtive() ?
        JSON.parse(localStorage.getItem('userObj')).data.role :
        '';
  }

  isUserSessionAtive() {
    return localStorage.getItem('userObj');
  }

  setUserDetails(userObj: any) {
    localStorage.setItem('userObj', JSON.stringify(userObj));
  }

  setNavtabItems(queues: any[]) {
    this.navtabItems = queues;
  }

  getNavtabItem(id: number) {
    return this.navtabItems.filter(item => (item.pathId === id));
  }

  getAllQueues() {
    return this.navtabItems;
  }

  setSelectedQueueId(id: number) {
    this.selectedQueueId = id;
  }

  getSelectedQueueId() {
    return this.selectedQueueId;
  }

  getQueueByPathname(name: string) {
    return this.navtabItems.filter(item => (item.path === name));
  }
}
