import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {Login} from 'src/app/core/model/login/login';
import {ParticleJson} from 'src/app/core/model/login/particle-inputs';
import {AuthService} from 'src/app/core/services/global/authentication.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {
  constructor(
      private dataService: DataService, private authService: AuthService,
      private router: Router, private formBuilder: FormBuilder) {}
  myStyle: object = {};
  myParams: object = {};
  width = 100;
  height = 100;

  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  ngOnInit() {
    this.myStyle = ParticleJson.PARTICLE_PARAMS.styles;

    this.myParams = ParticleJson.PARTICLE_PARAMS.params;
    this.firstFormGroup =
        this.formBuilder.group({firstCtrl: ['', Validators.required]});
    this.secondFormGroup =
        this.formBuilder.group({secondCtrl: ['', Validators.required]});
  }
}
