import {Component, OnInit, Input} from '@angular/core';
import { chartVariables } from '../chart-variables';
import { DataService } from 'src/app/core/services/serviceBridge/data.service';
import { Subject, timer } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LightermaskEventService } from 'src/app/core/services/lightermask-event.service';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-monitor-charts',
  templateUrl: './monitor-charts.component.html',
  styleUrls: ['./monitor-charts.component.scss']
})
export class MonitorChartsComponent implements OnInit {
  inputData: any = {};
  chartData:any = {};
  chartOptions:any = {};
  private killRefershCharts: Subject<void> = new Subject();

  constructor(private dataService: DataService,
              private lightermaskEvent: LightermaskEventService) {}

  @Input()
  set chartInput(data: any) {
    if (data) {
      this.inputData = {};
      this.inputData = data;
      this.chartData = {};
      if(data.refresh === "auto") {
        timer(0, 30000)
        .pipe(
            // This kills the request if the user closes the component
            takeUntil(this.killRefershCharts),
            )
        .subscribe(t => {
          this.getChartData(data.fromDate, data.toDate);
        });
      } else {
        this.getChartData(data.fromDate, data.toDate);
      }
    }
  }

  @Input()
  set manualRefresh(val: any) {
    if(val) {
      this.getChartData(this.inputData.fromDate, this.inputData.toDate);
    }
  }

  getChartData(fDate, tDate) {
    this.lightermaskEvent.mask('maskChart', 'Loading Chart..');
    const obj = {
      fromDate: fDate,
      toDate: tDate,
      tenant_id: coreUtils.removeHifen(window.location.hostname)
    }
    this.dataService.getEachCardDetails(obj, this.inputData.route).subscribe(
      resp => {
        this.lightermaskEvent.unMask('maskChart');
        this.processChart(resp.data)
      },
      (error) => {
        this.lightermaskEvent.unMask('maskChart');
      });
  }

  processChart(respData?: any) {
    switch(this.inputData.chart_type) {
      case 'column':
        this.chartData.data = respData.chart_data;
        this.chartData.chartOptions = chartVariables.COLUMN_GRAPH;
        this.setHeightWidth();
        this.chartData.columns = respData.columns;
        this.chartData.type = 'column';
        break;
      case 'stacked_column':
          this.chartData.data = respData.chart_data;
          this.chartData.chartOptions = chartVariables.STACKED_COLUMN_GRAPH;
          this.setHeightWidth();
          this.chartData.columns = respData.columns;
          this.chartData.type = 'stacked_column';
        break;
      case 'bar':
          this.chartData.data = respData.chart_data;
          this.chartData.chartOptions = chartVariables.BAR;
          this.setHeightWidth();
          this.chartData.columns = ['Queue Name', 'No. of Invoices', { role: 'style' }];
          this.chartData.type = 'bar';
        break;
      case 'pie':
          this.chartData.data = respData.chart_data;
          this.chartData.chartOptions = chartVariables.PIE;
          this.setHeightWidth();
          this.chartData.columns = respData.columns;
          this.chartData.type = 'pie';
        break;
      default:
        break;
    }

  }

  setHeightWidth() {
    this.chartData.chartOptions['height'] = this.inputData.chartHeight - 60
    this.chartData.chartOptions['width'] = this.inputData.chartWidth - 30
  }

  getWidth(data) {
    return (data.X && data.X <= 4) ? data.X * 25 : 100      //return as percentange
  }

  getHeight(data) {
    return (data.Y && data.Y <= 4) ? data.Y * 150 : 150*4     //Return as pixel
  }

  getcolumnNames(event) {
    console.log(event)
  }

  ngOnInit() {  }

  getTitle(obj: any) {
    const key = Object.keys(obj)[0];
    return key;
  }

  getData(obj: any) {
    const key = Object.keys(obj)[0];
    return obj[key];
  }

  checkIfAllEmpty(obj: any) {
    const key = Object.keys(obj)[0];
    let allEmpty = true;
    if(key && key.length > 0) {
      obj[key].forEach(element => {
        if(element[1] !== 0) {
          allEmpty = false;
        }
      });
    }
    return allEmpty;
  }

  onError(event: any) {
    event.message = '';
  }

  ngOnDestroy() {
    this.killRefershCharts.next();
  }

}