
import {ComboFieldData} from '../combo-field-data';
import {FormFieldConfig} from '../form-field-config';

import {FormField} from './form-field';

/* tslint:disable:no-any */
export class DropdownFormField extends FormField {
  constructor(
      fieldDef: FormFieldConfig,
      value: boolean|number|string|{[key: string]: string}|ComboFieldData) {
    super(fieldDef, value);
  }


  readValue(val: any): string {
    if (val === undefined || val === null) {
      return '';
    } else {
      return val;
    }
  }

  getType() {
    return 'dropdown';
  }

  getDefaultValue() {
    return this.readValue(this.value);
  }

  getView() {
    return 'dynamic-dropdown';
  }

  setValidations() {}

  setParentValidations() {
    super.setValidations();
  }
}
