import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatSort, MatDialogConfig, MatDialog } from '@angular/material';
import { Subject, timer } from 'rxjs';
import { AuthService } from 'src/app/core/services/global/authentication.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from 'src/app/core/services/serviceBridge/data.service';
import { MaskEventService } from 'src/app/core/services/mask-event.service';
import { takeUntil } from 'rxjs/operators';
import { ExceptionTableModel } from 'src/app/core/model/exceptions/exception-table-model';
import { SelectionModel } from '@angular/cdk/collections';
import { ButtonType } from 'src/app/field-exception/model/button-model';
import { CallbackPopupComponent } from 'src/app/field-exception/callback-popup/callback-popup.component';
import { StatusService } from 'src/app/core/services/statusmessage/status.service';
import { ScanFileViewComponent } from '../scan-file-view/scan-file-view.component';

@Component({
  selector: 'app-scanner',
  templateUrl: './scanner.component.html',
  styleUrls: ['./scanner.component.scss']
})
export class ScannerComponent implements OnInit {
  buttons = [];
  displayedColumns = [];
  dataSource = new MatTableDataSource();
  pathId = '';
  initialSelection = [];
  allowMultiSelect = true;
  selection = new SelectionModel<any>(true, []);
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  private killTrigger: Subject < void > = new Subject();
  
  constructor(private dataService: DataService, private maskEvent: MaskEventService,
    private router: Router, private route: ActivatedRoute,
    private authService: AuthService, private dialog: MatDialog,
    private statusService: StatusService) {}
    
    ngOnInit() {
    this.pathId = this.route.snapshot.queryParams.pathId;
    this.selection = new SelectionModel<any>(this.allowMultiSelect, this.initialSelection);

    this.maskEvent.mask('maskApp', 'Loading Exceptions..');
    this.getScanDocuments();
    timer(0, 30000)
      .pipe(
        // This kills the request if the user closes the component
        takeUntil(this.killTrigger),
      )
      .subscribe(t => {
        if(this.selection.selected.length === 0) {
          this.getScanDocuments();
        }
      });
  }

  getScanDocuments() {
    this.dataService.getScannerDocs(this.pathId)
      .subscribe(
        (response) => {
          this.maskEvent.unMask('maskApp');
          if (response.data && response.data.columns && response.data.columns.length > 0) {
            this.buttons = response.data.buttons;
            this.displayedColumns = response.data.columns;
            this.displayedColumns.splice(0,0,'select');
            this.dataSource = new MatTableDataSource < ExceptionTableModel > (
              response.data.files);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          }
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnDestroy() {
    this.killTrigger.next();
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }

  buttonAction(btn: ButtonType) {
    switch (btn.confirmation) {
      case 1:
        this.openDialog(btn);
        break;

      case 0:
        this.serviceCall(btn);
        break;

      default:
        // this.openDialog(btn);
        break;
    }
  }

  openDialog(btn: ButtonType) {
    const dialogConfig = new MatDialogConfig();

    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '300px';
    dialogConfig.data = {
      heading: 'Confirm',
      text: btn.confirmation_message,
    };

    const dialogRef = this.dialog.open(CallbackPopupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.serviceCall(btn);
      }
    });
  }

  showPdf(row) {
    const dialogConfig = new MatDialogConfig();

    this.selection.select(row);

    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '50%';
    dialogConfig.height = '80%';
    dialogConfig.data = {
      buttons: this.buttons,
      case_id: row.case_id,
    };

    const dialogRef = this.dialog.open(ScanFileViewComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      if (result.close !== true) {
        this.serviceCall(result.close, result.name);
      }
    });
  }

  serviceCall(btn: ButtonType, case_id?: string) {
    this.maskEvent.mask('maskApp', 'Loading..');
      const paramsArray = [];
      const obj: any = {};
      obj.case_id_list = [];
      if(!case_id) {
        this.selection.selected.filter((each)=> {
          obj.case_id_list.push(each.case_id)
        });
      } 
      else {
        obj.case_id_list.push(case_id)
      }
      obj.parameters = {'btn_name': btn.text, 'case_id_list': obj.case_id_list}
      obj.route = btn.functions[0].route;

      paramsArray.push(obj);

    this.dataService.dynamicServiceCall(paramsArray)
      .subscribe(
        resp => {
          this.maskEvent.unMask('maskApp');
          if (resp.flag) {
              this.statusService.post(resp.status_type, resp.message, 5);
          } else {
            this.statusService.post('error', resp.message, 5);
          }
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  checkButtonPerm = () => {
    return this.selection.selected.length > 0 ? false : true
  }

}
