import {OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs';
/* tslint:disable:no-any */
export class BasicComponent implements OnDestroy, OnInit {
  subscriptions: {[key: string]: Subscription} = {};

  ngOnInit() {}

  ngOnDestroy() {
    Object.keys(this.subscriptions)
        .forEach(key => this.removeSubscription(key));
  }

  removeSubscription(name: string) {
    if (this.subscriptions[name]) {
      this.subscriptions[name].unsubscribe();
      delete this.subscriptions[name];
    }
  }

  createSubscription(name: string, subscription: any) {
    if (this.subscriptions[name]) {
      this.removeSubscription(name);
    }
    this.subscriptions[name] = subscription;
  }
}
