import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {MaskEventService} from 'src/app/core/services/mask-event.service';

@Component({
  selector: 'app-email-rule-dialog',
  templateUrl: './email-rule-dialog.component.html',
  styleUrls: ['./email-rule-dialog.component.scss']
})
export class EmailRuleDialogComponent implements OnInit {
  constructor(private maskEvent: MaskEventService) {}
  screenHeight: number;
  isSwitchedOn = false;
  submitted = false;

  rulesForm = new FormGroup({
    from: new FormControl('', [Validators.required, Validators.email]),
    to: new FormControl('', [Validators.required, Validators.email]),
    cc: new FormControl('', [Validators.required, Validators.email]),
    bcc: new FormControl('', [Validators.required, Validators.email]),
    subject: new FormControl('', Validators.required),
    body: new FormControl('', Validators.required),
    attachment: new FormControl('', Validators.required)
  });

  get form() {
    return this.rulesForm.controls;
  }

  ngOnInit() {
    this.maskEvent.windowHeight.subscribe(
        height => (this.screenHeight = height - 370));
  }

  // onNoClick(): void {
  //   this.dialogRef.close();
  // }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.rulesForm.invalid) {
      return false;
    }
    console.log(JSON.stringify(this.rulesForm.value));
    return true;
  }
}
