import {forkJoin, Observable, of, Subject} from 'rxjs';
import {map, tap} from 'rxjs/operators';

import {AjaxService} from '../ajax.service';
import {DataResponse} from '../dataResponse';

/* tslint:disable:no-any */
export class UserRepository<T> {
  constructor(protected http: AjaxService) {}

  /**
   * Call to save the user to database
   * @param obj contains the body and the url and type of method
   * @returns Observable<DataResponse<any>> returns the response
   */
  createUser(obj: any): Observable<DataResponse<any>> {
    const params = {};
    params[''] = obj[''];
    params[''] = obj[''];
    const url = '';
    return this.http.ajaxCall(params, 'post', url)
        .pipe(tap((resp: DataResponse<any>) => {
          if (resp.success) {
            resp.data = obj;
          }
        }));
  }
}
