import {CommonModule} from '@angular/common';
import {NgModule} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {AppRoutingModule} from '../app-routing.module';
import {MaterialModule} from '../core/material/material.module';
import {SharedModule} from '../shared/shared.module';

import {DynamicFormButtonsComponent} from './dynamic-form-buttons/dynamic-form-buttons.component';
import {DynamicFormComplexFieldDirective} from './dynamic-form-complex-fields/dynamic-form-complex-field.directive';
import {DynamicFormFieldComponent} from './dynamic-form-field/dynamic-form-field.component';
import {DynamicFormSimpleFieldComponent} from './dynamic-form-simple-field/dynamic-form-simple-field.component';
import {DynamicFormTableComponent} from './dynamic-form-table/dynamic-form-table.component';
import {GenerateFormComponent} from './generate-form/generate-form.component';
import { DateAdapter } from '@angular/material';
import { DateFormat } from '../shared/dateformat/date-format';

@NgModule({
  declarations: [
    GenerateFormComponent, DynamicFormFieldComponent,
    DynamicFormComplexFieldDirective, DynamicFormSimpleFieldComponent,
    DynamicFormButtonsComponent, DynamicFormTableComponent
  ],
  imports: [
    BrowserAnimationsModule, CommonModule, AppRoutingModule, BrowserModule,
    MaterialModule, ReactiveFormsModule, FormsModule, SharedModule
  ],
  exports: [DynamicFormFieldComponent, DynamicFormButtonsComponent],
  providers: [{ provide: DateAdapter, useClass: DateFormat }],
  entryComponents: [DynamicFormSimpleFieldComponent],
})
export class DynamicformsModule {
  constructor(private dateAdapter:DateAdapter<Date>) {
		dateAdapter.setLocale('en-in'); // DD/MM/YYYY
	}
}
