import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import { MatSort, MatTableDataSource, MatDialogConfig, MatDialog} from '@angular/material';
import {ActivatedRoute, Router} from '@angular/router';
import {Subject, timer} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {ExceptionTableModel} from 'src/app/core/model/exceptions/exception-table-model';
import {AuthService} from 'src/app/core/services/global/authentication.service';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {FieldStoreService} from '../service/field-store.service';
import { FailedFileViewComponent } from '../failed-file-view/failed-file-view.component';
import { StatusService } from 'src/app/core/services/statusmessage/status.service';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-failed-file-list',
  templateUrl: './failed-file-list.component.html',
  styleUrls: ['./failed-file-list.component.scss']
})
export class FailedFileListComponent implements OnInit {
  displayedColumns = [];
  isEditdisabled = false;
  isDeleteDisabled = true;
  dataSource = new MatTableDataSource();
  pathId = '';
  @ViewChild(MatSort) sort: MatSort;
  private killTrigger: Subject<void> = new Subject();
  paginatorData = {};
  currentPageNumber = 1;
  numberofRecordsPerPage = 20;
  max: number = 100;
  stroke: number = 6;
  radius: number = 20;
  fileSelected = false;

  constructor(
      private dataService: DataService, private maskEvent: MaskEventService,
      private route: ActivatedRoute, private store: FieldStoreService,
      private dialog: MatDialog, private statusService: StatusService) {}

  ngOnInit() {
    this.pathId = this.route.snapshot.queryParams.pathId;
  
    this.maskEvent.mask('maskApp', 'Loading ..');
    // this.getExceptions();
    timer(0, 30000)
        .pipe(
            // This kills the request if the user closes the component
            takeUntil(this.killTrigger),
            )
        .subscribe(t => {
          if (this.currentPageNumber === 1 && !this.fileSelected) {
            this.getExceptions(1, this.numberofRecordsPerPage);
          }
        });
  }

  getExceptions(start, end) {
    const params = {
      queue_id : this.pathId,
      start: start,
      end: end
    }
    this.dataService.getExceptions(params)
      .subscribe(
        (response) => {
          this.maskEvent.unMask('maskApp');
          if(response.data && response.data.columns && response.data.columns.length > 0) {
            this.displayedColumns = response.data.columns;
            this.store.setTabsandFields(response.data);
            this.dataSource = new MatTableDataSource<ExceptionTableModel>(
                response.data.files);
            this.dataSource.sort = this.sort;
            setTimeout(() => {
              this.dataSource.sort = this.sort;
            }, (1000))
            this.paginatorData = response.data.pagination;
          }
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  getNextOrPreviousRecords(event) {
    this.currentPageNumber = event.pageNumber;
    this.maskEvent.mask('maskApp', 'Loading ..');
    this.getExceptions(event.start, event.end)
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  openDialog(item: any) {
    if(this.checkEditPermission(item)) {
      this.fileSelected = true;

      const dialogConfig = new MatDialogConfig();

      dialogConfig.disableClose = true;
      dialogConfig.autoFocus = true;
      dialogConfig.width = '50%';
      dialogConfig.height = '80%';
      dialogConfig.data = {
        case_id: item.case_id,
      };

      const dialogRef = this.dialog.open(FailedFileViewComponent, dialogConfig);
  
      dialogRef.afterClosed().subscribe(result => {
        if (result.service) {
          this.serviceCall(result.file_name);
        }
      });
    }
  }

  checkEditPermission(element: any) {
    return (element.operator ? (element.operator.toString().length > 0 ? false : true): true)  && (!element.case_lock);
  }

  serviceCall(case_id?: string) {
    this.maskEvent.mask('maskApp', 'Loading..');
      const paramsArray = [];
      const obj: any = {};
      obj.case_id = case_id;
      obj.queue = 'failed';
      obj.tenant_id = coreUtils.removeHifen(window.location.hostname);

    this.dataService.movetoVerify(obj)
      .subscribe(
        resp => {
          this.maskEvent.unMask('maskApp');
          if (resp.flag) {
              this.statusService.post(resp.status_type, resp.message, 5);
              this.getExceptions('', '')
          } else {
            this.statusService.post('error', resp.message, 5);
          }
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  ngOnDestroy() {
    this.killTrigger.next();
  }

}

