import { Directive, ElementRef, Renderer2, HostListener, Input } from '@angular/core';

@Directive({
  selector: '[appToolTipCustom]'
})
export class TooltipcustomDirective {
  @Input('appToolTipCustom') tooltipTitle: any;
  @Input() position: string;
  @Input() delay = 100;
  topp:any
  leftt:any
  tooltip: HTMLElement;

  offset = 10;

  constructor(private el: ElementRef, private renderer: Renderer2) { }

  @HostListener('mouseenter')
  onMouseEnter() {
    if (!this.tooltip) {
      this.show();
    }
  }

  @HostListener('mouseleave')
  onMouseLeave() {
    if (this.tooltip) {
      this.hide();
    }
  }

  show() {
    if (this.tooltipTitle !== '') {
      this.create();
      this.setPosition();
      this.renderer.addClass(this.tooltip, 'ng-tooltip-show');
    }
  }

  hide() {
    this.renderer.removeClass(this.tooltip, 'ng-tooltip-show');
    window.setTimeout(() => {
      this.renderer.removeChild(document.body, this.tooltip);
      this.tooltip = null;
    }, this.delay);
  }

  create() {
    if (this.tooltipTitle !== '') {
      let node = JSON.parse(this.tooltipTitle)
      let nodetitle = node[0];
      let content = node[1]

      this.tooltip = this.renderer.createElement('div');
      this.renderer.addClass(this.tooltip, 'tooltip-custom');

      let titleP = this.renderer.createElement('p');
      this.renderer.addClass(titleP, 'tooltip-title')
      let text1 = this.renderer.createText(nodetitle); 
      this.renderer.appendChild(titleP, text1);

      let bodyP = this.renderer.createElement('p');
      this.renderer.addClass(bodyP, 'tooltip-body')

      let keys_ = Object.keys(content);
      for(let i=0; i< keys_.length; i++){
        let content_text = keys_[i]+' : '+content[keys_[i]]
        let bodyPspan = this.renderer.createElement('span');
        let txt = this.renderer.createText(content_text)
        this.renderer.appendChild(bodyPspan, txt);
        this.renderer.appendChild(bodyP, bodyPspan);
      }

      this.renderer.appendChild(this.tooltip, titleP);
      this.renderer.appendChild(this.tooltip, bodyP);

      this.renderer.appendChild(document.body, this.tooltip);
    }
  }

  setPosition() {
    const hostPos = this.el.nativeElement.getBoundingClientRect();


    const tooltipPos = this.tooltip.getBoundingClientRect();


    const scrollPos = window.pageYOffset ||
        document.documentElement.scrollTop || document.body.scrollTop || 0;

    let top: number;
    let left: number;

    if (this.position === 'top') {
      top = hostPos.top - tooltipPos.height - this.offset;
      left = hostPos.left + (hostPos.width - tooltipPos.width) / 2;
    }

    if (this.position === 'bottom') {
      top = hostPos.bottom + this.offset;
      left = hostPos.left + (hostPos.width - tooltipPos.width) / 2;
    }

    if (this.position === 'left') {
      top = hostPos.top + (hostPos.height - tooltipPos.height) / 2;
      left = hostPos.left - tooltipPos.width - this.offset;
    }

    if (this.position === 'right') {
      top = hostPos.top + (hostPos.height - tooltipPos.height) / 2;
      left = hostPos.right + this.offset;
    }


    this.renderer.setStyle(this.tooltip, 'top', `${top + scrollPos}px`);
    this.renderer.setStyle(this.tooltip, 'left', `${left}px`);
  }

}
