import { Injectable } from '@angular/core';
declare let pdfjsLib: any;

@Injectable({
  providedIn: 'root'
})
export class PdfHandlerService {

  url: String;
  pdfDoc: any = {};
  pageObjectPromise: any = {};
  constructor() {
  }
  setPdfDocObjects(data, url) {
    return new Promise((resolve, reject) => {
      this.url = '../../assets/pdf/' + data.name;
      pdfjsLib.getDocument(url).then((doc) => {
        this.pdfDoc = doc;
        resolve(doc);
      }, (error) => {
        reject(error);
      });
    });
  }
}
